.hero {
  background: url("../images/court-building.jpg") no-repeat center center/cover;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;   /* Centers horizontally */
  align-items: center;       /* Centers vertically */
  text-align: center;
  color: white;
  position: relative;
  top: 0;
  left: 0;
  overflow: hidden;

  &::after {
    content: "";
    position: absolute;
    top: 0; left: 0; right: 0; bottom: 0;
    background: rgba(0, 0, 0, 0.5);
  }

  .hero-content {
    position: relative;
    z-index: 2;
    max-width: 800px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;  /* Ensures inner text also stays centered */
    height: 100%;             /* Takes full height for centering */

    h1 {
      font-size: 3rem;
      font-weight: 700;

      span {
        color: #f7c948;
      }
    }

    h3 {
      font-size: 1.5rem;
      margin: 1rem 0;
      font-weight: 500;
    }

    p {
      font-size: 1rem;
      margin-bottom: 1.5rem;
    }

    .btn-primary {
      padding: 0.8rem 2rem;
      background: #f7c948;
      border: none;
      font-weight: bold;
      font-size: 1rem;
      border-radius: 8px;
      cursor: pointer;
      transition: all 0.3s ease;

      &:hover {
        background: #ffb400;
        transform: translateY(-2px);
      }
    }
  }
}
